import React from 'react';
import './userBlock.css';

class Userblock extends React.Component {

  constructor(props) {
    super(props);
    this.state = {

    }
  }

  componentDidMount() {

  }

  handleRouter(url) {
    this.props.history.push('/' + url);
  }

  render() {
    return (
      <div className="userBlock">
        <div className="userBlock-top">
          <img src="./img/pic.jpg" className="l" alt="" />
          <div className="c">
            <div className="t"></div>
            <div className="b"><i></i><span className="iconfont icon-xingbienan"></span></div>
          </div>
          {
            this.props.isMe
            ?
              <span className="iconfont icon--shezhi r" onClick={() => this.handleRouter('setting')}></span>
            :
              <div className="follow-btn"></div>
          }
        </div>
        <div className="userBlock-numlist">
          <li onClick={() => this.handleRouter('follow')}><p>23</p><span></span></li>
          <li onClick={() => this.handleRouter('fans')}><p>4</p><span></span></li>
          <li><p>56</p><span></span></li>
        </div>
        <div className="userBlock-lable">
          <li></li><li></li><li>90</li><li></li>
        </div>
        <div className="userBlock-info">~emmmm</div>
      </div>
    );
  }
}

export default Userblock; 


